<template>
  <div class="page-originalPage">
    <!-- 头部 -->
    <!-- <CountDown #default="countDownSlot">
      <div class="head pl__30 pr__40">
        <span class=""> 活动截止：{{ countDownSlot.overTime }}</span>
        <VanCountDown class="text__white" :time="countDownSlot.overMinute">
          <template #default="timeData">
            活动仅剩
            <span class="text__time">{{ timeData.minutes }}</span> 分
            <span class="text__time">{{ timeData.seconds }}</span> 秒
          </template>
        </VanCountDown>
      </div>
    </CountDown> -->
    <!-- 顶部图片 -->
    <img
      class="img-auto"
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/BaiduABOriginalPage/header.png" />
    <!-- 手机号表单 -->
    <div class="form-title">- 开课3天内，对课程不满意无条件退还12元 -</div>
    <!-- 手机号 -->
    <div class="form-item mt__30">
      <p class="ml__10 mr__20">+86</p>
      <input
        ref="focusInput"
        placeholder="请输入手机号"
        v-model.trim="phone" />
    </div>
    <!-- 验证码 -->
    <div class="form-item mt__20">
      <input
        placeholder="请输入验证码"
        v-model.trim="code" />
      <CountDownBtn
        class="pl__20 phone-code "
        :phone="phone"
        :get-code-success-back="getCodeSuccessBack"
        :get-code-error-back="getCodeErrorBack"></CountDownBtn>
    </div>
    <!-- 理解报名按钮 -->
    <div class="form-btn mt__20" @click="bottomApplyClick">立即报名</div>
    <div class="line"></div>
    <div class="line-title">最新报名客户</div>
    <VanSwipe
      class="swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="swipe-item">
          <p>{{ el.name }}</p>
          <p>{{ el.phone }}</p>
          <p>{{ el.time }}</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <!-- 底部图片 -->
    <img
      :class="['img-auto', { mt__40: !index }]"
      v-for="(item, index) in 6"
      :key="index"
      :src="require(`@/assets/baiduABOriginalPage/${index + 1}.png`)" />
    <!-- 底部按钮 -->
    <!-- <CountPeople> -->
    <div class="submit-box">
      <div class="submit-topic">
        <!-- <div class="submit-topic__title">12元名额仅剩</div> -->
        <div class="submit-topic__number">
          仅售<span>12元</span>
        </div>
      </div>
      <div class="submit-btn" @click="bottomApplyClick">立即报名</div>
    </div>
    <!-- </CountPeople> -->
    <!-- 挽留弹窗 -->
    <BackDialog
      @backDiaLogSubmit="backDiaLogSubmit"></BackDialog>
    <!-- 支付类型弹窗 -->
    <PayTypeDialog
      @clickCloseIcon="closePayTypeDialog"
      @payTypeClick="payLogAndSubmitPay"
      :pay-dialog-config="payDialogConfig"></PayTypeDialog>
  </div>
</template>

<script>
import PayTypeDialog from '@/components/BaiduABBusniess/payTypeDialog'
import { Swipe, SwipeItem, Dialog } from 'vant'
import swipeList, { originalConfig } from './config'
import BackDialog from '@/components/BaiduABBusniess/backDialog'
import { openPayTypeDialog, payTypeClick, closePayTypeDialog } from '@/helper/payTypeDialog'
import CountDownBtn from '@/components/CountDownBtn'
// import CountDown from '@/components/CountDown'
// import CountPeople from '@/components/CountPeople'
import Store from '@/store'
import { clickLog } from '@/helper/clickLog'
import { initPhoneForm } from '@/helper/backDiaLog'
import { submitPay, isBuyOrder } from '@/helper/payMent'
import { getPayQuery, paySuccessBack, payErrorBack, getCodeSuccessBack, getCodeErrorBack } from '@/helper/baiduABPayMentBack'
import { isPhone } from '@/utils'

export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
    BackDialog,
    PayTypeDialog,
    // CountDown,
    // CountPeople,
    CountDownBtn
  },
  async beforeRouteEnter (to, form, next) {
    const groupInfo = JSON.parse(localStorage.getItem('groupInfo') || '{}')
    Store.$log.update({
      alternate: localStorage.getItem('throwOtherLog')
    })
    if (groupInfo.group !== 'A') {
      return next({ name: 'BaiduAB', query: to.query })
    }
    next()
  },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      timer: null, // 定时器
      paytype: 2, // 支付类型
      successPag: 'Officals', // 支付后要跳转的成功页面
      payDialogConfig: {
        dialogMark: false
      }, // 支付弹窗
      ...originalConfig, // 埋点数据
      swipeList // 轮播数组
    }
  },
  created () {
    this.isBuyOrder()
    this.initPhoneForm()
    this.timer = setInterval(async () => {
      const resultCode = await this.isBuyOrder()
      if (resultCode && resultCode !== 30000) {
        clearInterval(this.timer)
        this.timer = null
      }
    }, 3000)
    window.onpageshow = function (event) {
      if (event.persisted) {
        window.location.reload()
      }
    }
  },
  beforeDestroy () {
    this.timer && clearInterval(this.timer)
  },
  methods: {
    // 判断是否有缓存有效手机号
    initPhoneForm,
    openPayTypeDialog,
    payTypeClick,
    closePayTypeDialog,
    submitPay,
    isBuyOrder,
    paySuccessBack,
    payErrorBack,
    getCodeSuccessBack,
    getCodeErrorBack,
    getPayQuery,
    /**
     * @Date: 2021-01-14 15:03:00
     * @author: zjs
     * @description: 回调弹窗继续支付
     */
    backDiaLogSubmit () {
      const otherQuery = JSON.parse(localStorage.getItem('lastPayQuery'))
      this.submitPay({
        apiQuery: this.getPayQuery({
          ...otherQuery,
          return_url: encodeURIComponent(location.href)
        }),
        successBack: this.paySuccessBack,
        errorBack: this.payErrorBack
      })
    },
    /**
     * @Date: 2021-01-14 14:40:47
     * @author: zjs
     * @description: 支付类型埋点和支付
     */
    payLogAndSubmitPay (paytype) {
      this.payTypeClick(paytype)
      this.submitPay({
        apiQuery: this.getPayQuery({
          pay_type: paytype,
          return_url: encodeURIComponent(location.href)
        }),
        successBack: this.paySuccessBack,
        errorBack: this.payErrorBack
      })
    },
    /**
     * @Date: 2021-01-12 18:24:46
     * @author: zjs
     * @description:底部提交按钮被点击
     */
    bottomApplyClick () {
      clickLog({
        module_ori: '底部悬浮栏',
        module_part: this.BOTTOM_BTN
      })
      const { phone, code } = this
      if (!isPhone(phone)) {
        const msg = phone ? '手机号码有误，请重填' : '请填写手机号'
        Dialog({ message: msg }).then(() => {
          this.$refs.focusInput.focus()
          clickLog({
            module_ori: '表单错误模块',
            module_part: this.ERROR_PHONE
          })
        })
        return
      }
      this.$apis.payment
        .payBeforeCheck({ phone, code })
        .then(async (res) => {
          this.openPayTypeDialog()
        })
        .catch(err => {
          localStorage.setItem('guide_phone', phone)
          localStorage.setItem('guide_order_num', err.data.order_num)
          if (err.code === 30006) {
            this.$router.push({
              name: 'Officals',
              query: {
                ...this.$route.query
              }
            })
          }
        })
    }
  }
}
</script>
<style lang="scss">
.page-originalPage {
  width: 100%;

  img {
    vertical-align: middle;
  }

  .head {
    @include verCenter;

    color: #fff;
    font-size: 30px;
    justify-content: space-between;
    height: 85px;
    background: #5f99ff;
    box-shadow: 0 2px 20px 0 rgba(33, 91, 188, 0.2);
  }

  .text__white {
    color: #fff;
  }

  .text__time {
    color: #ffd316;
  }

  .img-auto {
    width: 100%;
  }

  .form-title {
    font-size: 26px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
    margin-top: 35px;
  }

  .form-item {
    margin-left: auto;
    margin-right: auto;
    width: calc(100% - 160px);
    height: 84px;
    border: 0;
    background: #eff2f8;
    font-size: 32px;
    font-weight: 400;
    color: #2d2d2d;
    display: flex;

    * {
      height: 100%;
    }

    p {
      @include horVerCenter;
    }

    .phone-code {
      font-size: 29px;
      font-weight: 400;
      color: #b92525;
    }

    input {
      background-color: transparent;
      border: 0;
      padding-left: 30px;
      width: calc(100% - 230px);

      &::placeholder {
        color: #b9b7b7;
        font-size: 32px;
        font-weight: 400;
      }
    }
  }

  .form-btn {
    @include horVerCenter;

    margin: 20px auto 0;
    width: calc(100% - 160px);
    font-size: 36px;
    font-weight: 400;
    color: #fff;
    height: 84px;
    background: linear-gradient(180deg, #ff929d 0%, #ff0034 100%);
  }

  .line {
    margin: 60px auto 0;
    height: 1px;
    width: calc(100% - 100px);
    border-top: 1px dashed #b3bbc5;
  }

  .line-title {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
  }

  .swipe-box {
    margin-left: 72px;
    height: 100px;
    margin-top: 30px;

    .swipe-item {
      width: calc(100% - 100px);
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      font-weight: 400;
      line-height: 50px;
      color: #434c5d;
    }
  }

  .submit-box {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 108px;
    background: #fff;
    border: 1px solid #b0b0b0;
    display: flex;

    .submit-topic {
      width: 40%;

      .submit-topic__title {
        font-size: 24px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #333;
        text-align: center;
        margin-top: 15px;
      }

      .submit-topic__number {
        margin-top: 30px;
        font-size: 26px;
        font-weight: bold;
        color: #666;
        text-align: center;

        span {
          margin-left: 10px;
          font-size: 40px;
          font-weight: 600;
          text-align: center;
          color: #ff2c12;
        }
      }
    }

    .submit-btn {
      @include horVerCenter;

      width: 60%;
      height: 100%;
      background: linear-gradient(180deg, #ff929d 0%, #ff0034 100%);
      font-size: 46px;
      font-weight: 600;
      color: #fffcff;
    }
  }
}
</style>
